import React, { useContext, useState } from 'react'
import { ThemeContext, ThemeProvider } from 'styled-components'
import themeDefault from './default'
import themeDark from './dark'
import themeLight from './light'
import { ThemeConfig } from './type'

const themeConfigs: any = {
  default: themeDefault,
  dark: themeDark,
  light: themeLight
}

export const useTheme = (): ThemeConfig => {
  return useContext<ThemeConfig>(ThemeContext);
}

export const ThemeWrapper = ({ children }: { children: React.ReactNode }) => {
  const [theme, setTheme] = useState(themeConfigs.default)
  theme['setTheme'] = (value: string) => {
    !!themeConfigs[value] && setTheme(themeConfigs[value])
  }

  return (
    <ThemeProvider theme={theme}>
      {children}
    </ThemeProvider>
  )
}
