/* eslint-disable react/forbid-foreign-prop-types */
const BasePropTypes = require("prop-types");
let PropTypes = Object.create(BasePropTypes);
// let PropTypes: any = {};

let fromComponent;
if (process.env.NODE_ENV !== "production") {
  fromComponent = function(c: any, attr: string) {
    const propTypes = c.propTypes || {};
    if (attr === undefined) {
      return propTypes;
    }
    return propTypes[attr];
  };
} else {
  fromComponent = function(c: any, attr: string) {
    if (attr === undefined) {
      return {};
    }
    return PropTypes.any;
  };
}

Object.assign(PropTypes, {
  fromComponent
});

export default PropTypes;
