import React from 'react'
import PropTypes from '@app/core/propTypes'
import { GlobalStyle } from '@app/ui/styles/global';
import { GridThemeProvider } from 'styled-bootstrap-grid';
import { ThemeWrapper } from './theme/Context';

import { library } from '@fortawesome/fontawesome-svg-core'
import { fas } from '@fortawesome/pro-solid-svg-icons';

library.add(fas)

const Root = ({ isLoading, children }: { isLoading?: boolean, children: React.ReactChild }) => {
  return (
    <React.Fragment>
      <ThemeWrapper>
        <GlobalStyle />
        <GridThemeProvider>{children}</GridThemeProvider>
      </ThemeWrapper>
    </React.Fragment>
  )
}

Root.propTypes = {
  isLoading: PropTypes.bool
}

export default Root
